import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { SharedModule } from '../../../shared/modules/shared.module';
import { BillOfMaterialRoutingModule } from './bill-of-material-routing.module';
import { BillOfMaterialComponent } from './bill-of-material.component';
import { BillOfMaterialAddComponent } from './bill-of-material-add/bill-of-material-add.component';
import { BillOfMaterialEditComponent } from './bill-of-material-edit/bill-of-material-edit.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    BillOfMaterialRoutingModule
  ],
  declarations: [
    BillOfMaterialComponent,
    BillOfMaterialAddComponent,
    BillOfMaterialEditComponent
  ],
  entryComponents:[
    BillOfMaterialAddComponent,
    BillOfMaterialEditComponent
  ]
})
export class BillOfMaterialModule { }



