import { Component, OnInit, Inject, Output, EventEmitter, ViewChild, ElementRef } from '@angular/core';
import { FormGroup, FormControl, FormBuilder, Validators, FormArray } from '@angular/forms';
import { BillOfMaterialService, CurrentUserService,ErrorHandleService, ItemMasterService } from '../../../../shared';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { jqxWindowComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxwindow';

@Component({
  selector: 'app-bill-of-material-add',
  templateUrl: './bill-of-material-add.component.html',
  styleUrls: ['./bill-of-material-add.component.scss']
})
export class BillOfMaterialAddComponent implements OnInit {

  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('myValidator') myValidator: jqxValidatorComponent;
  @ViewChild('msgNotification') msgNotification: jqxNotificationComponent;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @ViewChild('eventWindow') eventWindow: jqxWindowComponent;

  @ViewChild('inputContainer') private inputContainer:ElementRef;
  @ViewChild('outputContainer') private outputContainer:ElementRef;

  @Output() AddedData = new EventEmitter();
  @Output() cancelData = new EventEmitter();

  bomForm: FormGroup;
  apiUrl: string;

  inputItemAdapter:any = [];
  inputItemFocus: any = [];
  
  outputItemAdapter:any = [];
  outputItemFocus: any = [];

  constructor(
    private fb: FormBuilder,
    private ehs: ErrorHandleService,
    private cus: CurrentUserService,
    private ims: ItemMasterService,
    private boms: BillOfMaterialService,
    @Inject('API_URL') apiUrl: string, ) {
    this.apiUrl = apiUrl;
    this.createForm();
  }

  ngOnInit() {
  }

  createForm() {
    this.bomForm = this.fb.group({
      'code': [null, Validators.required],
      'name': [null, Validators.required],
      'status':[null, Validators.required],
      'input_materials': this.fb.array([
        this.initMaterials(),
      ]),
      'output_materials': this.fb.array([
        this.initMaterials(),
      ])
    })
  }

  initMaterials(){
    return this.fb.group({
      item_id: [null, Validators.required],
      item_name: [null],
      qty: [null, Validators.required],
    });
  }

    /**
   * inputItemFilter Event is called when Item input field has
   * keyup action followed by 'Enter'
   * Generate Suggestion based on input value entered
   * @param searchString 
   * @param index 
   */
  inputItemFilter(searchPr, index) {
      let keycode = searchPr['keyCode'];
      if ((keycode == 40)) {
        document.getElementById('itemCode1').focus();
      }
      let searchString = searchPr['target'].value;
      let len = searchString.length;
      let dataString = searchString.substr(len - 1, len);
      let temp = searchString.replace(' ', '');
      temp = temp.replace(/\s/g, '');
      if (dataString == ' ' && temp.length > 1) {
        if (temp) {
          this.inputItemFocus[index] = true;
          this.scrollToBottom('inputContainer');
          this.ims.index({code:temp, transact:1, limit:'all'}).subscribe(
            response => {
              // console.log(response);
              this.inputItemAdapter[index] = [];
              this.inputItemAdapter[index] = response;
              // console.log(this.inputItemAdapter);
            },
            error => {
              console.log(error);
            }
          );
        } else {
          this.inputItemFocus[index] = false;
        }
      }
  }

  outputItemFilter(searchPr, index) {
      let keycode = searchPr['keyCode'];
      if ((keycode == 40)) {
        document.getElementById('itemCode2').focus();
      }
      let searchString = searchPr['target'].value;
      let len = searchString.length;
      let dataString = searchString.substr(len - 1, len);
      let temp = searchString.replace(' ', '');
      temp = temp.replace(/\s/g, '');
      if (dataString == ' ' && temp.length > 1) {
        if (temp) {
          this.outputItemFocus[index] = true;
          this.scrollToBottom('outputContainer');
          this.ims.index({code:temp, transact:1, limit:'all'}).subscribe(
            response => {
              this.outputItemAdapter[index] = [];
              this.outputItemAdapter[index] = response;
            },
            error => {
              console.log(error);
            }
          );

        } else {
          this.outputItemFocus[index] = false;
        }
      }
  }

  /**
   * Event fired when option is selected from Item Suggestion Select field
   * Hide Select field after Item Selected.
   * @param selectedEvent 
   * @param index 
   */
  inputItemListSelected(selectedEvent, index) {
    // console.log(selectedEvent);
    if (selectedEvent && selectedEvent.target && selectedEvent.target.value) {
      let displayText = selectedEvent.target.selectedOptions[0].text;
      this.bomForm.get('input_materials')['controls'][index].get('item_id').patchValue(selectedEvent.target.value);
      this.bomForm.get('input_materials')['controls'][index].get('item_name').patchValue(displayText);
      this.inputItemFocus[index] = false;
    }
  }

  outputItemListSelected(selectedEvent, index) {
    // console.log(selectedEvent);
    if (selectedEvent && selectedEvent.target && selectedEvent.target.value) {
      let displayText = selectedEvent.target.selectedOptions[0].text;
      this.bomForm.get('output_materials')['controls'][index].get('item_id').patchValue(selectedEvent.target.value);
      this.bomForm.get('output_materials')['controls'][index].get('item_name').patchValue(displayText);
      this.outputItemFocus[index] = false;
    }
  }

  /**
   * Add FormGroup to Requisition Item FormArray
   * Increments Requestion Item FormArray
   */
  addItem(arrayName) {
    const control1 = <FormArray>this.bomForm.controls[arrayName];
    control1.push(this.initMaterials());
    // console.log(control1.length);
  }

  /**
   * Remove FormGroup at particular position form Requisition Item FormArray
   * Decrements Requestion Item FormArray
   */
  removeItem(i: number, arrayName) {
    const control1 = <FormArray>this.bomForm.controls[arrayName];
    control1.removeAt(i);
    /**
     * Remove itemAdapter itemArray at Particular position i, 
     * Select Field option for select field at position 'i' of formArray
     */
    if(arrayName == 'output_materials'){
      this.outputItemAdapter.splice(i, 1);
    }else{
      this.inputItemAdapter.splice(i, 1);
    }
    
  }

  /**
   * Open Modal window to confirm before
   * saving form data
   */
  confirmSave(){
    this.eventWindow.open();
  }

  /**
   * Event Triggered when confirm modal is closed.
   * @param event 
   */
  eventWindowClose(event: any): void {
    // console.log(event);
    if (event.type === 'close') {
        if (event.args.dialogResult.OK) {
          // console.log('OK Pressed');
          this.store();
        } else if (event.args.dialogResult.Cancel) {
          // console.log('Cancel Pressed');
        }
    }
  }

  scrollToBottom(containerName){
    try {
      setTimeout(() => {
        if(containerName == 'inputContainer'){
          this.inputContainer.nativeElement.scrollTop = this.inputContainer.nativeElement.scrollHeight;
        }else{
          this.outputContainer.nativeElement.scrollTop = this.outputContainer.nativeElement.scrollHeight;
        }
      },100);
    } catch(err) {
      console.log(err);
    }
  }

    /**
     * Function triggered when save is confirmed
     * @param formData 
     */
    store() {
      let formData = this.bomForm.getRawValue();
      formData['bomItems'] = [];
      for(let i = 0; i < formData['input_materials'].length; i++){
        formData['input_materials'][i].type = 1;
        formData['bomItems'].push(formData['input_materials'][i]);
      }

      for(let i = 0; i < formData['output_materials'].length; i++){
        formData['output_materials'][i].type = 0;
        formData['bomItems'].push(formData['output_materials'][i]);
      }
      // console.log(formData);
      this.jqxLoader.open();
      this.boms.store(formData).subscribe(
        result => {
          let data = this.ehs.formatError(result);
  
          if (data['result'] == true) {
            this.AddedData.emit(data);
          } else {
            let messageDiv: any = document.getElementById('windowMessage');
            messageDiv.innerHTML = '';
            for (let i = 0; i < data['message'].length; i++) {
              let errorDiv = document.createElement('li');
              errorDiv.innerText = data['message'][i];
              messageDiv.appendChild(errorDiv);
            }
            this.winNotification.open();
          }
          this.jqxLoader.close();
  
        },
        error => {
          this.jqxLoader.close();
          console.info(error);
        }
      );
    }

    cancelBtn() {
      this.cancelData.emit(true);
    }

}



